<template>
	<header>
		<ul>
			<li class="return" v-on:click="back(who)" ><img src="../../../static/img/return.png"/></li>
			<li>{{title}}</li>
			<li></li>
		</ul>
	</header>
</template>

<script>
	export default {
		//APPHeaderwho 
		props:['title','who'],
		methods:{
			back(who){
				switch(who){
					case "log":{
						this.$store.state.myPage_show = true;
						break;
					}
					case "detail":{
						this.$store.state.discoverDetail_show = false;
						break;
					}
					case "close":{
						this.$store.state.discoverClose_show = false;
						break;
					}
					case "explore":{
//						this.$emit('returnBack',{bol:false})
						this.$store.state.judge = false;
						console.log(1)
						break;
					}
					case "more":{
						this.$store.state.judgeMore = false;
						break;
					}
					case "home":{
						this.$store.state.judgeHome = false;
						break;
					}
					//
					case "collect":{
						this.$store.state.collect = false;
						break;
					}
					//
					case "foot":{
						this.$store.state.foot = false;
						break;
					}
					//
					case "post":{
						this.$store.state.post = false;
						break;
					}
					//
					case "load":{
						this.$store.state.load = false;
						break;
					}
					//
					case "reg":{
						this.$store.state.reg = false;
						break;
					}
					
				}
				 
		}
	}
	}
</script>
	
<style scoped="scoped">
	/*scoped  */
	header{
		width: 100%;
		height: 1.133333rem;
		background: #48382b;
		text-align: center;
		line-height: 1.133333rem;
		font-size: 0.426666rem;
		color: white;
		z-index: 999;
		position: fixed;
		top: 0;
		left: 0;
	}
	.return{
		position: absolute;
		left: 0.133333rem;
		top: 0.25rem;
		width: 0.533333rem;
		height: 0.533333rem;	
		
	}
	.return img{
		width: 100%;
	}	
</style>


