<template>
	<div class="wrap">
		<app-header :title="title" :who="who"></app-header>
		<div class="not" v-if="!noload">
			<div class="icon"><img src="../../../static/img/nullimg.png"/></div>
			<p></p>
			<div class="go" v-on:click="goto"><img src="../../../static/img/doload.png"/></div>
		</div>
		<input type="file"  id="file" value="" multiple="true" v-on:change="onchange"/>
		<div v-if="noload">
			<div class="go golast" v-on:click="go"><img src="../../../static/img/doload.png"/></div>
			<ul id="load" class="clear"></ul>
		</div>
		
	</div>
</template>

<script type="text/javascript">
	import AppHeader from "../common/AppHeader2"
	import { Toast } from 'mint-ui'
	export default{
		components:{
			AppHeader
		},
		data:function(){
			return{
				title:"",
				who: "load",
				noload:"",
			}
			
		},
		computed: {
		  loadimg () {
	        return this.$store.state.loadimg
	      }
		},
		methods:{
			go(){
				var file = document.getElementById("file");
				var ul = document.getElementById("load");
				file.click();
			},
			goto(){
				this.noload = true;
			},
			onchange(){
				var _this = this;
				var file = document.getElementById("file");
				var ul = document.getElementById("load");
				//window.URL.createObjectURL()URLURL
				var src = window.URL.createObjectURL(file.files[0]);
				var img = new Image();
				//createObjectURLURL,URL
				//URL.revokeObjectURL(),
				//
				img.onload = function(){
					window.URL.revokeObjectURL(src);
					var imgLi = document.createElement("li");
					imgLi.appendChild(this);
					ul.appendChild(imgLi);
					}
				Toast({
					  message: '',
					  iconClass:"mintui mintui-field-success",
					  duration: 1000,
					  className:"tip"
					});
					img.src = src;
					_this.$store.state.loadimg = true;
				}
		},
		mounted:function(){
			var ul = document.getElementById("load");
			if(this.loadimg){
				this.noload = true
			}else{
				this.noload = false
			}
		}
	}
	
</script>

<style scoped="scoped">
	.clear:after{
		content: "";
		display: block;
		clear: both;
	}
	.not{
		position: absolute;
		text-align: center;
		top: 6rem;
		left: 3.7rem;
	}
	.icon{
		width: 1.2rem;
		height: 1.2rem;
	}
	.icon img{
		margin-left: 0.7rem;
		width: 100%;
	}
	.not p:nth-child(2){
		margin-top: 0.4rem;
		font-size: 0.4rem;
		color: rgba(190,190,190,0.9);
	}
	.go{
		width: 2.666666rem;
	  font-size: 0.373333rem;
	  color: white;
	  line-height: 0.8rem;
	  background: #cbb91d;
	  border-radius: 0.133333rem;
	  margin-top: 0.4rem;
	  text-align: center;
	}
	.go p{
		margin-left: 0.5rem;
		display: inline-block;
	}
	.go img{
		width: 0.7rem;
		margin-left: 0.02rem;	
	}
	/**/
	#file{
		display: none;
	}
	.golast{
		
		margin-top: 1.4rem;
		margin-bottom: 0.4rem;
		margin-left: 3.666666rem;
	}
	#load{
		width: 8.666666rem;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		padding-left: 0.666666rem;
		padding-right: 0.666666rem;
	}
</style>


