<template>
	<!---->
	<div class="homePage">
		<app-header v-show="!judgeMore" :header="header" @goback="goback"></app-header>
		<div class="wrap" style="width: 100%;" v-if="!disP">
		<swiper :swiperSlides="swiperSlides"></swiper>
		<div id="line"></div>
		<!---->
		<div class="scale">
			<p class="title"></p>
			<p class="more" @click="moreScale"></p>
			<div class="scale_content">
				<div class="scale_list" v-for="(item,index) in scale" :key="index" @click="goDetail(index)">
					<p>{{item.title}}</p>
					<div class="scale_list_time">
						<div>
							<img src="../../assets/timeOut.png" alt="" />:
							<img src="../../assets/timeOut.png" alt="" />:
							<img src="../../assets/timeOut.png" alt="" />
						</div>
						<div id="scale_time">						
							<span>0{{item.time.hour}}</span>
							<span>{{item.time.minute>9?'':0}}{{item.time.minute}}</span>
							<span>{{item.time.second>9?'':0}}{{item.time.second}}</span>
						</div>
						<span id="scale_txt">{{item.scale}}</span>
					</div>
					<div class="img">
						<img :src="item.src" alt="" />
					</div>
				</div>
			</div>
		</div>
		<!---->
		<div class="recommend">
			<p class="title"></p>
			<p class="more" @click="moreRecommend"></p>
			<div class="recommend_content">
				<div class="recommend_left" @click="homeToDetail_one">
					<img :src="recommend[3].pic" alt="" />
					<div class="recommend_shop">
						<p>{{recommend[3].name}}</p>
						<p>{{recommend[3].money}}</p>
					</div>
				</div>
				<div class="recommend_right">
					<div>
						<img :src="recommend[1].pic" alt="" />
						<div class="recommend_shop">
							<p>{{recommend[1].name}}</p>
							<p>{{recommend[1].money}}</p>
						</div>
					</div>
					<div>
						<img :src="recommend[0].pic" alt="" />
						<div class="recommend_shop">
							<p>{{recommend[0].name}}</p>
							<p>{{recommend[0].money}}</p>
						</div>
					</div>
					<div>
						<img :src="recommend[2].pic" alt="" />
						<div class="recommend_shop">
							<p>{{recommend[2].name}}</p>
							<p>{{recommend[2].money}}</p>
						</div>
					</div>
				</div>
			</div>
		</div>
		</div>
		<!---->
		<transition name="move">
			<!--value-->
			<more v-if="disP" :style="{left:ll}" @getBol="getbol" :value="value"></more>
		</transition>
		<transition name="moveHome">
			<detail id="homeDetail" v-if="judgeHome" :style="{left:homell}" :which="which"></detail>
		</transition>
	</div>
</template>

<script>
	import axios from 'axios'; //axios
	import Swiper from "../common/Swiper";
	import AppHeader from "../common/AppHeader";
	import More from "./more";
	import Detail from '../discoverPage/DiscoverDetail';
	import pic1 from "../../assets/product1.png";
	import pic2 from "../../assets/product2.png";
	import pic3 from "../../assets/product3.png";
	import pic4 from "../../assets/product4.png";
	//js 
	export default {
		//
		data: function() {
			return {
				value: {},
				ll: '100%',
				homell: '100%',
				which: 'home',
				recommend: [{pic:''},{pic:''},{pic:''},{pic:''}],
				swiperSlides: [],
				timeOut: {},
				header: {title:"",bol:false},
				scale: [
						{title:'',time:{hour:1,minute:20,second:0},scale:'8.8',src:pic1},
						{title:'',time:{hour:2,minute:30,second:0},scale:'8.8',src:pic2}
				]
			}
		},
		//
		components: {
			Swiper,
			AppHeader,
			More,
			Detail
		},
		computed: {
	      	judgeMore () {
	        		return this.$store.state.judgeMore;
	        },
	        judgeHome() {
	        		return this.$store.state.judgeHome;
	        },
	        disP() {
	        		return this.$store.state.disP;
	        }
	     },
		methods: {
			//
			goDetail(index) {
				var that = this;
				console.log(this.scale[index].title);
				axios.get('/api/detail', {
					params: {
						id: this.scale[index].title
					}
				}).then(function(res) {
					that.$store.state.del = res.data.detail;
					that.$store.state.judgeHome = true;
					that.homell = '0%';
				}).catch(function(error) {
					console.log(error)
				});
			},
			//
			homeToDetail_one() {
				var that = this;
				axios.get('/api/detail', {
					params: {
						id: 1
					}
				}).then(function(res) {
					that.$store.state.del = res.data.detail;
					that.$store.state.judgeHome = true;
					that.homell = '0%';
				}).catch(function(error) {
					console.log(error)
				});
			},
			//
			moreScale() {
				this.$store.state.disP = true;
				this.ll = 0;
				this.value = {name:"moreScale"};
				this.header.bol = true;
			},
			moreRecommend() {
				this.$store.state.disP = true;
				this.ll = 0;
				this.value = {name:"moreRecommend"};
				this.header.bol = true;
			},
			getbol(msg) {
//				this.ll = msg.ll;
//				this.disP = false;
			},
			goback(msg) {
				console.log(msg);
				this.ll = '100%';
				this.$store.state.disP = false;
				this.header.bol = false;
			}
		},
		mounted: function() {
			var that = this;
			var time = new Date();
			this.timeOut.time = time.getTime();
			if (!localStorage.time) {
				localStorage.time = this.timeOut.time;
			}
			var second = parseInt((this.timeOut.time - localStorage.time) / 1000 % 60)
			var minute = parseInt((this.timeOut.time - localStorage.time) / 1000 / 60 % 60)
			axios.get("/api/home").then(function(res) {
				that.swiperSlides = res.data.swiperSlides;
				that.recommend = res.data.recommend;
				that.scale = res.data.scale;
				for (let i = 0; i < that.scale.length; i++) {
					if (that.scale[i].time.minute - minute >= 0) {
						that.scale[i].time.minute = that.scale[i].time.minute - minute;
					} else {
						that.scale[i].time.minute = 60 + that.scale[i].time.minute - minute;
					}
					if (that.scale[i].time.second - second >= 0) {
						that.scale[i].time.second = that.scale[i].time.second - second;
					} else {
						that.scale[i].time.second = 60 + that.scale[i].time.second - second;
						that.scale[i].time.minute --;
					}
					
				}
			}).catch(function(error) {
				console.log(error);
			});
//			
			setInterval(function (){
				for (let i = 0; i < that.scale.length; i++) {
					that.scale[i].time.second--;
					if (that.scale[i].time.second < 0) {
						that.scale[i].time.second = 59;
						that.scale[i].time.minute--;
						if (that.scale[i].time.minute < 0) {
							that.scale[i].time.minute = 59;
							that.scale[i].time.hour--;
							if (that.scale[i].time.hour < 0) {
								that.scale[i].time.hour = 1;
							}
						}
					}
				}
			},1000)
		},
		created() {
//			axios.get("/api.map.baidu.com/location/ip?ak=620crf2HSGl1vpSRXFkV66hPodqPszso&coor=bd09ll").then(function(res) {
//				console.log(res)
//			}).catch(function(error) {
//				console.log(error)
//			});
		}
	}
</script>

<style scoped="scoped">
	::-webkit-scrollbar{
		width: 0;
		height: 0;
		display: none;
	}
	.homePage{
		width: 100%;
		padding-top: 1.133333rem;
		padding-bottom: 1.333333rem;
		overflow: hidden;
		position: relative;
	}
	.scale{
		width: 100%;
		border-top: 0.266666rem solid #ebeae4;
		position: relative;
	}
	.recommend{
		width: 100%;
		border-top: 0.266666rem solid #ebeae4;
		position: relative;
	}
	.title{
		color: #6ab439;
		font-size: 0.373333rem;
		padding: 0.4rem 0 0.266666rem 0.4rem;
	}
	.more{
		font-size: 0.32rem;
		color: #d3d3d3;
		position: absolute;
		top: 0.4rem;
		right: 0.4rem;
	}
	/**/

	.scale_content{
		width: 9.2rem;
		/*height: 4.2rem;*/
		margin: 0 auto;
		box-sizing: border-box;
		display: -webkit-box;
		display: -webkit-flex;
		display: -ms-flexbox;
		overflow-x: scroll;
	}	
	.scale_list{
		/*display: flex;*/
		width: 4.4rem;
		padding-right: 0.3rem;
	}
	.scale_list>p:nth-of-type(1){
		text-align: center;
		color: #5b5b5b;
		font-size: 0.32rem;
		padding-bottom: 0.266666rem;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	#scale_txt{
		color: red;
		font-size: 0.4rem;
	}
	.scale_list .scale_list_time{
		height: 0.6rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
		position: relative;
	}
	.scale_list_time img{
		width: 0.533333rem;
	}
	.scale_list_time div{
		width: 2rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.scale_list_time #scale_time{
		color: white;
		position: absolute;
		left: 0;
		top: 0.08rem;
	}
	#scale_time span{
		text-align: center;
		width: 0.533333rem;
	}
	.scale_list .img{
		width: 4.4rem;
		height: 2.666666rem;
		overflow: hidden;
	}
	.img{
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0.133333rem 0;
	}
	.img img{
		width: 100%;
	}
	/**/
	.recommend_content{
		width: 93%;
		margin: 0 auto;
		display: flex;
		justify-content: center;
	}
	.recommend_left,.recommend_right{
		box-sizing: border-box;
	}
	.recommend_left{
		width: 4rem;
		height: 4rem;
		display: flex;
		justify-content: center;
		align-items: center;
		overflow: hidden;
		position: relative;
	}
	.recommend_right{
		width: 6rem;
		height: 4rem;
		display: flex;
		flex-wrap: wrap;
	}
	.recommend_right>div{
		width: 50%;
		height: 2rem;
		display: flex;
		justify-content: center;
		align-items: center;
		overflow: hidden;
		position: relative;
	}
	.recommend_right>div:nth-of-type(1){
		width: 100%;
		height: 2rem;
	}
	.recommend img{
		width: 100%;
	}
	/**/
	.recommend_shop{
		width: 100%;
		position: absolute;
		top: 0;
		left: 0;
		color: white;
		background: linear-gradient(rgba(0,0,0,0.5) 0%,transparent);
	}
	.recommend_shop{
		padding: 0.133333rem 0.133333rem;
		font-size: 0.266666rem;
		font-weight: lighter;
	}
	/**/
	.move-enter-active {
		transition: all .5s ease;
	}
	
	.move-leave-active {
		transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
	}
	
	.move-enter,
	.move-leave-to
	{
		transform :translate3d(100%,0,0);
		opacity: 0;
	}
	
	/**/

	.moveHome-enter-active {
		transition: all .5s ease;
	}
	
	.moveHome-leave-active {
		transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
	}
	
	.moveHome-enter,
	.moveHome-leave-to
	{
		transform :translate3d(100%,0,0);
		opacity: 0;
	}
	#homeDetail{
		position: absolute;
		top: 0;
		z-index: 998;
		padding-bottom: 1.333333rem;
		background: white;
	}
</style>


