<template>
	<div class="wrap">
		<div class="hand handmove" v-show="handBol"><img src="../../assets/hand.png" alt="" /></div>
		<div class="head">
			<img src="../../../static/img/gentleman.png"/>
			
		</div>
		<div class="scan">
			<p></p>
			<div v-on:click="scan"><img style="width: 20%;" src="../../../static/img/code.png"/></div>
		</div>
	</div>
</template>

<script>
	import { Toast } from 'mint-ui'
	export default{
		data:function(){
			return{
				handBol: false
			}
		},
		methods:{
			scan(){
				console.log(1)
				this.handBol = true
				Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
			}
		}
	}
</script>
	
<style scoped="scoped">
	img {
		width: 100%;
	}
	.wrap{
		width: 100%;
		height: 16.453333rem;
		background: url(../../../static/img/pointbg.png) no-repeat;
		background-size: 100% 100%;
		position: relative;
		overflow: hidden;
	}
	.head{
		width: 80%;
		background: #91755c;
		line-height: 3.333333rem;
		text-align: center;
		position: relative;
		margin: 0 auto;
		margin-top: 3.666666rem;
		font-size: 0.426666rem;
		border-radius: 0.4rem;
		color:white;
	}
	.head img{
		width: 2.666666rem;
		position: absolute;
		top: -1.333333rem;
		left: 50%;
		margin-left: -1.333333rem;
	}
	.scan{
		margin-top:1.6rem;
		width: 100%;
		text-align: center;
	}
	.scan p{
		font-size: 0.46rem;
		color: white;
		margin-bottom: 0.666666rem;
	}
	/**/
	.hand{
		position: absolute;
		right: 0.133333rem;
		top: 0.133333rem;
		width: 1.6rem;
	}
	.hand img{
		transform: rotateZ(45deg);
	}
	.handmove{
		animation: handmove 1s linear infinite alternate;
	}
	@keyframes handmove{
		from{
			opacity: 0.5;
			transform: translate3D(10px,-10px,0);
		}
		to{
			opacity: 1;
			transform: translateY(0px);
		}
	}
</style>


