<template>
	<div id="hot">
		<hr/>
		<div v-if="!discoverDetail_show" class="padding">
			<app-swiper :banner="picArr"></app-swiper>
		<!---->
			<ul class="shop"  v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10" >
				<li v-for="(item,index) in initial" v-on:click="detail(index)">
					<!---->
					<div class="picture">
						<img :src="item.first"/>
						<div class="love" v-on:click.stop="collect(index)">
							<img :src="love[item.collect?1:0]"/>
						</div>
					</div>
					<!---->
					<p class="name">{{item.name}}</p
					<!---->
					<div class="want clear">
						<div class="fl wantlove"><img src="../../../static/img/love-s.png"/></div>
						<ul class="fl clear">
							<li v-for="i in item.people" class="fl people"><img :src="i"/></li>
						</ul>
						<p class="fl count">{{item.people.length}}</p>
					</div>
				</li>
			</ul>
			<div class="nomore" v-if="nomore">
			    <span class="text"></span>
			</div>
		</div>
		<!---->
		<transition name="fade" mode="in-out">
				<discover-detail id="detail" v-if="discoverDetail_show" :which="details"></discover-detail>
		</transition>
	</div>
</template>
<script>
	import AppSwiper from "./DiscoverSwiper"
	import axios from 'axios';//
	import DiscoverDetail from "./DiscoverDetail"
	import { InfiniteScroll } from 'mint-ui';//
	import { Toast } from 'mint-ui'
	export default{
		//
		components:{
			AppSwiper,
			DiscoverDetail
		},
		computed: {
	      discoverDetail_show () {
	        return this.$store.state.discoverDetail_show
	      },
	        success(){
		  	return this.$store.state.success
		  }
	  },
		//
		data:function(){
			return{
				picArr:[],
				hotShop:[],//
				initial:[],//
				love:["../static/img/love.png","../static/img/love-s.png"],
				del:{},
				details:"detail",
				nomore:false
			}
		},
		methods:{
			collect(index){
				if(!this.success){
					Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
					return
				}
				console.log(1)
				this.initial[index].collect=!this.initial[index].collect
			},
			detail(index){
				this.$store.state.del=this.initial[index];
				this.$store.state.del.index = index;
				this.$store.state.discoverDetail_show = true;
			},
			loadMore() {
				if(!this.hotShop.length){
					this.loading = false;
					this.nomore = true;
					return
				}
			  this.loading = true;
			  if(this.hotShop.length>=6){
			  	//6
			  	for(let i=0;i<6;i++){
			  		this.initial.push(this.hotShop.splice(0,1)[0])
			  	}
			  }else if(this.hotShop.length<6){
			  	for(let i=0;i<this.hotShop.length;i++){
			  		this.initial.push(this.hotShop.splice(0,1)[0])
			  	}
			  }
			}
		},
		mounted:function(){
			var _this = this;
			axios.get("/DiscoverHot/activity")
				.then(function(response){
					_this.picArr = response.data.swiperBanner;
				})
			axios.get("/DiscoverHot/hotShop")
				.then(function(response){
					_this.hotShop = response.data.hotShop;
					_this.initial = _this.hotShop.splice(0,6)

				})
		}
	}
</script>

<style scoped="scoped">
	.fl{float:left}
	.clear:after{
		content: "";
		display: block;
		clear: both;
	}
	
	#hot{
		width: 100%;
		position: relative;	
	}
	.padding{
		padding-bottom: 100px;
	}
	.shop{
		width: 100%;
	}
	.shop>li{
		padding:0.533333rem;
		border-bottom: 0.346666rem solid rgba(190,190,190,0.3);
		background: white;
	}
	/**/
	.picture{
		width: 100%;
		position: relative;
		left: 0;
		top: 0;
		margin-bottom: 0.4rem;
		overflow: hidden;
		height: 6rem;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 0.133333rem;
	}
	.picture>img{
		width: 100%;
		/*height: 4.4rem;*/
	}
	.love{
		width: 0.746666rem;
		height:  0.746666rem;
		border-radius: 50%;
		background: rgba(0,0,0,0.5);
		position: absolute;
		right: 0.266666rem;
		top: 0.266666rem;
		text-align: center;
	}
	.love>img{
		width: 70%;
		margin-top:0.133333rem;
	}
	/**/
	.name{
		font-size: 0.4rem;
		margin-bottom: 0.4rem;
	}
	/**/
	.wantlove{
		width: 0.4rem;
		height: 0.4rem;
		margin-right: 0.133333rem;
		margin-top:0.066666rem;
	}
	.wantlove img{
		width: 100%;
	}
	.people{
		width:0.533333rem;
		height:0.533333rem;
		margin-right: 0.133333rem;
	}
	.people>img{
		width: 100%;
		height: 100%;
		border-radius: 50%;
	}
	.count{
		font-size: 0.293333rem;
		color: darkgray;
		margin-top: 0.133333rem;
	}
	/**/
	#detail{
		position: absolute;
		top: -1.1rem;
		left: 0;
		z-index: 999;
	}
	.fade-enter-active,.fade-leave-active{
		transition: all 0.3s ease-out; 
	}
	.fade-enter{
		transform:translateX(10rem);
	}
	.fade-leave-active{
		transform:translateX(10rem);
	}
	/**/
	.nomore{
        text-align:center;
        overflow:hidden;
        padding: 0.3rem;
    }
    .nomore .text{
        padding:10px 20px;
        position: relative;
        font-size: 0.34rem;
        line-height: 0.4rem;
        color: gray;
    }
    .nomore .text::before,
    .nomore .text::after{
        position:absolute;
        top:50%;
        border-top:1px solid gray;
        content:'';
        height:0;
        width:2.933333rem;
    }
    .nomore .text::before{
        right:100%;
    }
    .nomore .text::after{
        left:100%;
    }
</style>


