<template>
	<div class="wrap clear">
		<div class="bg">
			<div @click="change1">
				<router-link to="/discover" class="fl hot" :class="bol==1?'select':''"></router-link>
			</div>
			<div @click="change2">
				<router-link to="/close" class="fl close" :class="bol==2?'select':''" ></router-link>
			</div>
		</div>
		<discover-hot v-if="discover" class="discover"></discover-hot>
		<router-view class="view"></router-view>
	</div>
</template>

<script> 
	import DiscoverHot from "./DiscoverHot"
	export default{
		//
		components:{
			DiscoverHot
		},
		computed: {
	      discover () {
	        return this.$store.state.discover
	      }
	   },
		//
		data:function(){
			return{
				bol:1
			}
		},
		methods:{
			change1(){
				this.bol = 1;
				this.$store.state.discover = true;
			},
			change2(){
				this.bol = 2;
				this.$store.state.discover = false;
			}
		}
			
			
	}
</script>

<style scoped="scoped">
	.fl{float: left;}
	.clear:after{
		content: "";
		clear: both;
		display: block;
	}
	.wrap{
		width: 100%;
	}
	.bg{
		width: 100%;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 500;
		background: #48382b;
	}
	.wrap .hot,.wrap .close{
		width: 50%;
		position:relative;
		height: 1.1rem;		
		text-align: center;
		font-size: 0.4rem;
	}
	
	.wrap a ,.wrap a {
		color: dimgray;
		text-decoration: none;
		line-height: 1.1rem;
}
	.wrap .select {
		color: palevioletred;		
	}
	.wrap .select:after{
		width: 100%;
		height: 0.053333rem;
		background: url(../../../static/img/line.png) no-repeat;
		background-size: 100%;
		content: "";
		position: absolute;
		left: 0;
		bottom: -0.028rem;
	}
	.discover{
		margin-top: 1.1rem;
	}
	.view{
		margin-top: 1.1rem;
	}
</style>


