<template>
	<div class="wrap">
		<transition name="trans" mode="in-out">
			<person v-on:login="login" v-if="myPage_show" :info="info"></person>
		</transition>
		<transition name="move" mode="in-out">
			<log v-if="!myPage_show" class="login"></log>
		</transition>
	</div>	
</template>

<script>
	import log from "./log"
	import person from "./person"
	export default{		
		components:{
			person,
			log
		},
		computed: {
	      myPage_show () {
	        return this.$store.state.myPage_show
	      },
	      user(){
	      	return this.$store.state.user
	      },
	      success(){
	      	return this.$store.state.success
	      }
	   },
	   watch:{
	   	user:function(val,oldVal){
	   			var data = val
				this.info.head = data.head;
				this.info.name = data.name;
				this.info.font = "font";
				this.info.fontstyle = "fontstyle";
				this.info.collect = data.collect;
				this.info.message = data.message;
				this.info.score = data.score;
				this.info.background = data.background;
				if(this.success){
					this.info.font = "font";
					this.info.fontstyle = "fontstyle";
				}else{
					this.info.font = "fontbefore";
					this.info.fontstyle = "fontstylebefore";
				}
	   	}
	   },
		data:function(){
			return{
				info:{
					background:"../static/img/white.png",
					head:"../static/img/notlog.png",
					font:"fontbefore",
					name:"",
					fontstyle:"fontstylebefore",
					collect:"",
					message:"",
					score:"",
				}
			}
		},
		methods:{
			login(data){
				this.show = data;
			},

	}
	}
</script>

<style scoped="scoped">
	.wrap{
		width: 100%;
		position: relative;
	}
	.login{
		width: 100%;
		position: absolute;
		top: 0;
	}
	.move-enter-active,.move-leave-active{
		transition: all 0.5s ease-out; 
	}
	.move-enter{
		transform:translateX(10rem);
		opacity: 0;
	}
	.move-leave-active{
		transform:translateX(10rem);
		opacity: 0;
	}
	.trans-enter-active,.trans-leave-active{
		transition: all 0.5s ease-out; 
	}
	.trans-enter{
		opacity: 0;
	}
	.trans-leave-active{
		opacity: 0;
	}
</style>


