<template>
	<div class="collect">
		<app-header :title="title" :who="who"></app-header>
		<div class="not">
			<div class="icon"><img src="../../../static/img/nullscore.png"/></div>
			<p>0</p>
			<div class="go" v-on:click="go"> <img src="../../../static/img/add.png"/></div>
		</div>
	</div>
</template>

<script>
	import AppHeader from "../common/AppHeader2"
	export default{
		components:{
			AppHeader
		},
		data:function(){
			return{
				title:"",
				who:"collect"
			}
		},
		methods:{
			go(){
				this.$router.push({path:'/explore'});
				this.$store.state.collect = false;
			}
		}
	}
</script>

<style scoped="scoped">
	.collect{
		width: 100%;
		min-height: 16.453333rem;
		position: absolute;
	}
	.not{
		position: absolute;
		text-align: center;
		top: 6rem;
		left: 3.7rem;
	}
	.icon{
		width: 1.2rem;
		height: 1.2rem;
	}
	.icon img{
		margin-left: 0.7rem;
		width: 100%;
	}
	p{	
		margin-top: 0.3rem;
		font-size: 0.4rem;
		color: rgba(190,190,190,0.9);
	}
	.go{
		width: 2.666666rem;
	  font-size: 0.373333rem;
	  color: white;
	  line-height: 0.8rem;
	  background: #62086d;
	  border-radius: 0.133333rem;
	  margin-top: 0.4rem;
	  text-align: center;
	}
	.go img{
		width: 0.6rem;
		margin-top: 0.11rem;	
		margin-left: 0.05rem;
	}
</style>


