<template>
	<div class="wrap">
		<app-header :title="title" :who="who"></app-header>
		<div class="not">
			<div class="icon"><img src="../../../static/img/foot.png"/></div>
			<p></p>
			<div class="go" v-on:click="go"><p>Go</p><img src="../../../static/img/run.png"/></div>
		</div>
	</div>
</template>

<script>
	import AppHeader from "../common/AppHeader2"
	export default{
		components:{
			AppHeader
		},
		data:function(){
			return{
				title:"",
				who: "foot"
			}
			
		},
		methods:{
			go(){
				this.$router.push({path:'/discover'});
				this.$store.state.foot = false;
			}
	}
	}
</script>

<style scoped="scoped">
	.not{
		position: absolute;
		text-align: center;
		top: 6rem;
		left: 3.7rem;
	}
	.icon{
		width: 1.2rem;
		height: 1.2rem;
	}
	.icon img{
		margin-left: 0.7rem;
		width: 100%;
	}
	.not p:nth-child(2){
		margin-top: 0.4rem;
		font-size: 0.4rem;
		color: rgba(190,190,190,0.9);
	}
	.go{
		width: 2.666666rem;
	  font-size: 0.373333rem;
	  color: white;
	  line-height: 0.8rem;
	  background: palevioletred;
	  border-radius: 0.133333rem;
	  margin-top: 0.4rem;
	  text-align: center;
	}
	.go p{
		margin-left: 0.5rem;
		display: inline-block;
	}
	.go img{
		width: 0.5rem;
		margin-top: 0.18rem;	
		margin-left: 0.15rem;
	}
</style>


