<template>
	<div class="detail">
		<app-header :title="title" :who="who" @returnBack="returnBack"></app-header>
		<div class="detail_more" v-show="!mapBol">
			<!---->
			<div class="picture">
				<img :src="del.first" />
				<div class="love" v-on:click.stop="collect(del.collect)">
					<img :src="love[a?1:0]" />
				</div>
			</div>
			<!---->
			<ul class="ctn">
				<!---->
				<li>
					<!---->
					<p class="name">{{del.name}}</p <!---->
					<div class="want clear">
						<div class="fl wantlove"><img src="../../../static/img/love-s.png" /></div>
						<ul class="fl clear">
							<li v-for="i in del.people" class="fl people"><img :src="i" /></li>
						</ul>
						<p class="fl count">{{del.people.length}}</p>
					</div>
					<!---->
					<ul class="clear location">
						<li class="fl icon"><img src="../../../static/img/position.png" /></li>
						<li class="fl address">{{del.address}}</li>
						<li class="fr position" @click="map(del.address)">Go ></li>
					</ul>
				</li>
				<!---->
				<li>
					<div class="clear titel">
						<div class="fl icon"><img src="../../../static/img/note.png" /></div>
						<p class="fl state"></p>
						<p class="fr price">{{del.introduce[0]}}</p>
					</div>
					<ul class="explain">
						<li v-for="item in filter">{{item}}</li>
					</ul>
				</li>
				<!---->
				<li>
					<div class="clear titel">
						<div class="fl icon"><img src="../../../static/img/food.png" /></div>
						<p class="fl state"></p>
					</div>
					<ul class="food clear">
						<li v-for="i in del.recommend" class="fl">{{i}}</li>
					</ul>
				</li>
				<!---->
				<li>
					<div class="clear titel user">
						<div class="fl icon"><img src="../../../static/img/talk.png" /></div>
						<p class="fl state"></p>
					</div>
					<ul class="talk">
						<li v-for="i in del.user">
							<div>
								<img :src="i.head" />
								<p>{{i.name}}</p>
							</div>
							<div>{{i.talk}}</div>
						</li>
					</ul>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import AppHeader from "../common/AppHeader2";
	import { Toast } from 'mint-ui'
	export default {
		props: ['which'],
		components: {
			AppHeader
		},
		//,,
		computed: {
			filter: function() {
				return this.del.introduce.slice(1);
			},
			del: function() {
				return this.$store.state.del
			},
			success(){
		  		return this.$store.state.success
		  	}
		},
		data: function() {
			return {
				love: ["../static/img/love.png", "../static/img/love-s.png"],
				a: this.$store.state.del.collect,
				title: "",
				who: this.which,
				mapBol: false,
				center: {lng: 113.271931,
					lat: 23.147308}
			}
		},
		methods: {
			returnBack(msg) {
				console.log(msg)
			},
			collect(data) {
				if(!this.success){
					Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
					return
				}
				this.a = !this.a;
			},
			map(address) {
				var that = this;
				$.ajax({
					type: "get",
					url: "http://api.map.baidu.com/place/v2/search",
					data: {
						query: address,
						region: '',
						output: 'json',
						ak: '620crf2HSGl1vpSRXFkV66hPodqPszso'
					},
					async: true,
					dataType: 'jsonp',
					success: function(res) {
						that.center.lng = res.results[0].location.lng
						that.center.lat = res.results[0].location.lat
						console.log(that.center)
						that.$store.state.center = that.center;
						that.mapBol = true;
					},
					error: function(err) {
						console.log(err)
					}
				});

				
			}
		}
	}
</script>

<style scoped="scoped">
	.fl {
		float: left
	}
	
	.fr {
		float: right
	}
	
	.clear:after {
		content: "";
		display: block;
		clear: both;
	}
	
	.show {
		display: none;
	}
	
	.detail {
		width: 100%;
		overflow: scroll;
		height: 16.453333rem;
		background: white;
	}
	
	.detail_more {
		padding-top: 1.133333rem;
	}
	
	.return {
		position: absolute;
		left: 0.133333rem;
		top: 0.133333rem;
		width: 0.533333rem;
		height: 0.533333rem;
		z-index: 999;
	}
	
	.return img {
		width: 100%;
	}
	
	.picture {
		width: 100%;
		position: relative;
	}
	
	.picture>img {
		width: 100%;
		height: 5.333333rem;
	}
	
	.love {
		width: 0.933333rem;
		height: 0.933333rem;
		border-radius: 50%;
		background: rgba(0, 0, 0, 0.5);
		position: absolute;
		right: 0.266666rem;
		bottom: 0.266666rem;
		text-align: center;
	}
	
	.love>img {
		width: 70%;
		margin-top: 0.16rem;
	}
	/**/
	
	.ctn {
		width: 100%;
	}
	
	.ctn>li {
		padding: 0.4rem;
		background: white;
		border-bottom: 0.266666rem solid rgba(190, 190, 190, 0.3);
	}
	/**/
	
	.name {
		font-size: 0.4rem;
		margin-bottom: 0.4rem;
	}
	/**/
	
	.wantlove {
		width: 0.4rem;
		height: 0.4rem;
		margin-right: 0.133333rem;
		margin-top: 0.066666rem;
	}
	
	.wantlove img {
		width: 100%;
	}
	
	.people {
		width: 0.533333rem;
		height: 0.533333rem;
		margin-right: 0.133333rem;
	}
	
	.people>img {
		width: 100%;
		height: 100%;
		border-radius: 50%;
	}
	
	.count {
		font-size: 0.293333rem;
		color: darkgray;
		margin-top: 0.133333rem;
	}
	/**/
	
	.location {
		margin-top: 0.4rem;
	}
	
	.icon {
		width: 0.5rem;
		height: 0.5rem;
		margin-right: 0.133333rem;
		margin-left: -0.1rem;
	}
	
	.icon img {
		width: 100%;
	}
	
	.address {
		font-size: 0.32rem;
		margin-top: 0.106666rem;
	}
	
	.position {
		font-size: 0.4rem;
		margin-top: 0.106666rem;
		color: palevioletred;
	}
	/**/
	
	.titel {
		width: 100%;
		margin-bottom: 0.266666rem;
	}
	/**/
	
	.titel .icon {
		width: 0.6rem;
		height: 0.6rem;
		margin-right: 0.266666rem;
		margin-top: -0.1rem;
	}
	
	.titel .state {
		font-size: 0.34rem;
		color: #989898;
	}
	
	.titel .price {
		font-size: 0.34rem;
		color: #dc0922;
	}
	
	.explain li {
		font-size: 0.34rem;
		margin-bottom: 0.186666rem;
	}
	/**/
	
	.food {
		width: 100%;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	
	.food li {
		text-align: center;
		width: 2.666666rem;
		font-size: 0.34rem;
		line-height: 0.666666rem;
		border: 0.013333rem solid darkgray;
		color: rgba(0, 0, 0, 0.9);
		margin-bottom: 0.346666rem;
	}
	/**/
	
	.user {
		margin-bottom: 0;
	}
	
	.talk li {
		padding: 0.4rem 0;
		border-bottom: 0.013333rem solid rgba(0, 0, 0, 0.3);
	}
	
	.talk li img {
		width: 0.5rem;
		height: 0.5rem;
		border-radius: 50%;
		margin-right: 0.133333rem;
		vertical-align: middle;
	}
	
	.talk li p {
		display: inline-block;
		font-size: 0.3rem;
		vertical-align: middle;
	}
	
	.talk li div:nth-child(2) {
		margin-top: 0.266666rem;
		font-size: 0.34rem;
	}
	
	.map {
		width: 100%;
		height: 600px;
	}
</style>


