<template>
	<div class="wrap wrap1">
		<transition name="trans" mode="in-out">
			<div v-show="!col">
				<div class="log">					
					<div class="head"><img :src="info.head"/></div>
					<p class="btn" v-on:click="log" :class="info.font">{{info.name}}</p>
					<ul :class="info.fontstyle">
						<li v-on:click="collect"> <span>{{info.collect}}</span></li>
						<li v-on:click="message"> <span>{{info.message}}</span></li>
						<li v-on:click="score"> <span>{{info.score}}</span></li>
					</ul>
					<div v-show="success" v-on:click="quit" class="quit"><img src="../../../static/img/quit.png"/></div>
				</div>
				<div class="background"><img :src="info.background"/></div>
				<div class="content">
					<ul class="section">
						<li v-on:click="nopay">
							<div><img src="../../../static/img/notpay.png"/></div>
							<p></p>
						</li>
						<li v-on:click="ready">
							<div><img src="../../../static/img/ready.png"/></div>
							<p></p>
						</li>
						<li v-on:click="notalk">
							<div><img src="../../../static/img/nottalk.png"/></div>
							<p></p>
						</li>
					</ul>
					<!-- -->
					<transition  name="bounce">
						<div class="browse" v-show="browse">
							<div class="null">
								<div><img :src="arr[page]"/></div>
								<p></p>
							</div>
						</div>
					</transition>
					<ul class="footer">
						<li class="clear" v-on:click="myfoot">
							<div class="fl"><img src="../../../static/img/myfoot.png"/></div>
							<p class="fl"></p>
							<div class="fr"><img src="../../../static/img/go.png"/></div>
						</li>
						<li class="clear" v-on:click="mypost">
							<div class="fl"><img src="../../../static/img/mypost.png"/></div>
							<p class="fl"></p>
							<div class="fr"><img src="../../../static/img/go.png"/></div>
						</li>
						<li class="clear" v-on:click="myload">
							<div class="fl"><img src="../../../static/img/load.png"/></div>
							<p class="fl"></p>
							<div class="fr"><img src="../../../static/img/go.png"/></div>
						</li>
						<li class="clear" v-on:click="myset">
							<div class="fl"><img src="../../../static/img/set.png"/></div>
							<p class="fl"></p>
							<div class="fr"><img src="../../../static/img/go.png"/></div>
						</li>
					</ul>
				</div>
			</div>
		</transition>
		<transition name="move">
			<router-view class="view" v-show="col"></router-view>
		</transition>
		<!---->
		<transition name="move">
			<my-foot class="foot" v-show="foot"></my-foot>
		</transition>
		<!---->
		<transition name="move">
			<my-post class="post" v-show="post"></my-post>
		</transition>
		<!---->
		<transition name="move">
			<my-load class="load" v-show="load"></my-load>
		</transition>
	</div>
</template>

<script>
	import { Toast } from 'mint-ui'
	import MyFoot from "./MyFoot"
	import MyPost from "./MyPost"
	import MyLoad from "./MyLoad"
	export default{
		components:{
			MyFoot,
			MyPost,
			MyLoad
		},
		props:["info"],
		computed: {
	      	success () {
	        return this.$store.state.success
	      },
		    col () {
		     return this.$store.state.collect
		  },
		  	foot(){
		  		return this.$store.state.foot
		  },
		  	post(){
		  		return this.$store.state.post
		  },
		  	load(){
		  		return this.$store.state.load
		  },
	  },
		data:function(){
			return{
				browse:false,
				page:0,
				arr:["../../../static/img/pay.png","../../../static/img/null.png","../../../static/img/nomessage.png","../../../static/img/pay.png","../../../static/img/null.png","../../../static/img/nomessage.png"]
			}
		},
		methods:{
			log(){
				if(this.success){
					return
				}
				this.$store.state.myPage_show = false;
			},
			quit(){
				//
				this.$store.state.myPage_show = false;
				this.$store.state.success = false;
				//
				this.$store.state.user = {
				
								background:"../static/img/white.png",
								head:"../static/img/notlog.png",
								name:"",
								collect:"",
								message:"",
								score:""
							}
				Toast({
					  message: '',
					  iconClass:"mintui mintui-field-success",
					  duration: 1000,
					  className:"tip"
					});
				
			},
			//
			collect(){
				if(!this.$store.state.success){
					Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
					return
				}else{
					this.$store.state.collect = true;
					console.log(this.$store.state.collect)
					this.$router.push({path:'/collect'})
					
				}
			},
			//
			message(){
				if(!this.$store.state.success){
					Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
					return
				}else{
					
					this.$store.state.collect = true;
					console.log(this.$store.state.collect)
					this.$router.push({path:'/message'})
				}
			},
			//
			score(){
				if(!this.$store.state.success){
					Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
					return
				}else{
					this.$store.state.collect = true;
					this.$router.push({path:'/score'})
				}
			},
			//
			nopay(){
				if(!this.$store.state.success){
					Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
					return
				}else{
					if(this.page == 3){
						this.browse = false;
						this.page = 0;
						return
					}
					this.page = 3,
					this.browse = true;
				}
				
			},
			//
			ready(){
				if(!this.$store.state.success){
					Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration:1000,
					  className:"tip"
					});
					return
				}else{
					if(this.page == 4){
						this.browse = false;
						this.page = 1;
						return
					}
					this.page = 4,
					this.browse = true;
				}
			},
			//
			notalk(){
				if(!this.$store.state.success){
					Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
					return
				}else{
					if(this.page == 5){
						this.browse = false;
						this.page = 2;
						return
					}
					this.page = 5,
					this.browse = true;
				}
			},
			//
			myfoot(){
				if(!this.$store.state.success){
					Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
					return
				}else{
					let $wrap1 = $(".wrap1");
					let h = $wrap1.innerHeight();
					let $foot = $(".foot");
					$foot.css("height",h-100)
					this.$store.state.foot = true
					
				}
			},
			//
			mypost(){
				if(!this.$store.state.success){
					Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
					return
				}else{
					let $wrap1 = $(".wrap1");
					let h = $wrap1.innerHeight();
					let $post = $(".post");
					$post.css("height",h-100)
					this.$store.state.post = true
				}
			},
			//
			myload(){
				if(!this.$store.state.success){
					Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
					return
				}else{
					let $wrap1 = $(".wrap1");
					let h = $wrap1.innerHeight();
					let $load = $(".load");
					$load.css("height",h-100)
					this.$store.state.load = true
				}
			},
			//
			myset(){
				if(!this.$store.state.success){
					Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
					return
				}
			}
	}
}
</script>

<style scoped="scoped">
	.fl{float: left;}
	.fr{float: right;}
	.clear:after{
		content: "";
		display: block;
		clear: both;
	}
	.wrap{
		width: 100%;
		position: relative;
		padding-bottom: 100px;
		height: 16.453333rem;
		overflow: scroll;
	}
	/* */
	.browse{
		min-height: 2.666666rem;
		background: rgba(190,190,190,0.1);
	}
	.null{
		width: 100%;
		text-align: center;
	}
	.browse .null{
		padding-top: 0.4rem;
	}
	.browse .null img{
		width: 1rem;
		height: 1rem;
	}
	.browse .null{
		font-size: 0.38rem;
		color: #48382b;
	}
	.browse .null p{
		margin-top: 0.4rem;
	}
	
	/*   */ 
	.view{
		position:absolute;
		top: 0;
	}
	.log{
		padding: 0.2rem 1rem;
		text-align: center;
		border-bottom: 0.133333rem solid #f4f3f0;
		position: relative;
		z-index: 100;
	}
	/**/
	.quit{
		width: 0.533333rem;
		height: 0.533333rem;
		position: absolute;
		top: 0.5rem;
		left: 9rem;
	}
	.quit img{
		width: 100%;
		height: 100%;
	}
	.background{
		width: 100%;
		height: 6.866666rem;
		position: absolute;
		top: 0;
	}
	.background img{
		width: 100%;
		height: 100%;
		z-index: 10;
	}
	.head{
		margin-top: 1.6rem;
	}
	.head img{
		width: 2rem;
		height: 2rem;
		border-radius: 50%;
	}
	.btn{
		width: 2rem;
		height:0.6rem;
		line-height: 0.6rem;
		font-size: 0.4rem;
		display: inline-block;
		margin-top: 0.6rem;
	}
	.fontbefore{
		border: 0.066666rem solid #c3ad88;
		border-radius: 0.266666rem;
		color: #ccb99b;
	}
	.font{
		color: white;
		border: none;
	}
	.log ul{
		width: 100%;
		display: flex;
		justify-content:space-around;
		border-top: 0.013333rem solid #c5c4c4;
		padding: 0.133333rem 0;
		margin-top: 0.6rem;
	}
	.log ul li{
		flex-grow: 1;
		text-align: center;
		font-size: 0.34rem;
		line-height: 0.666666rem;	
	}
	.fontstylebefore{
		color: #bbbaba;
	}
	.fontstyle{
		color: white;
	}
	.log ul li:nth-child(1) span{
		color: #71f704;
	}
	.log ul li:nth-child(2) span{
		color: #3191a4;
	}
	.log ul li:nth-child(3) span{
		color: #9104b9;
	}
	.log ul li:nth-child(2){
		border-left:0.013333rem solid  #c5c4c4;
		border-right:0.013333rem solid  #c5c4c4;
	}
	
	.content{
		padding:0 0.4rem;
	}
	.section{
		width: 100%;
		display: flex;
		justify-content: space-around;
		margin-top: 0.8rem;
		border-bottom: 1px solid #332022;
		padding: 0 0 0.8rem;
	}
	.section li{
		width: 1.333333rem;
		text-align: center;
		
	}
	.section img{
		width: 0.8rem;
		height: 0.8rem;
	}
	.section p{
		margin-top: 0.266666rem;
		font-size: 0.32rem;
		color: #636262;
	}
	.view{
		width: 100%;
		min-height: 2.666666rem;
	}
	.footer{
		width: 100%;
	}
	.footer li {
		line-height: 1.2rem;
		border-bottom: 1px solid #332022;
		font-size:0.34rem;
	}
	.footer li div img{
		vertical-align: middle;
	}
	.footer li div:nth-child(1) img{
		width: 0.6rem;
		height: 0.6rem;
	}
	.footer li div:nth-child(3) img{
		width: 0.4rem;
		height: 0.4rem;		
	}
	.footer li p{
		margin-left: 0.4rem;
		padding-top: 0.066666rem;
		color: #5e5147;
	}
	/**/
	.move-enter-active,.move-leave-active{
		transition: all 0.5s ease-out; 
	}
	.move-enter{
		transform:translateX(10rem);
		opacity: 0;
	}
	.move-leave-active{
		transform:translateX(10rem);
		opacity: 0;
	}
	.trans-enter-active,.trans-leave-active{
		transition: all 0.5s ease-out; 
	}
	.trans-enter{
		opacity: 0;
	}
	.trans-leave-active{
		opacity: 0;
	}
	/**/
	.foot,.post,.load{
		width: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 100;
		background: white;
	}
</style>


