<template>
	<header>
		<img class="logo" src="../../../static/img/gentleman.png"/>
		<div class="city">
			<img src="../../assets/back.png" v-show="header.bol" @click="back"/>
			<p>{{city}}</p>
		</div>
		<p class="weather"></p>
	</header>
</template>

<script>
	export default {
		props:['header'],
		data() {
			return {
				city: '',
			}
		},
		methods: {
			back() {
				this.$emit('goback',{bol:false});
				this.$store.state.disP = false;
			}
		},
		created() {
			var that = this;
			$.ajax({
				type:"get",
				url:"https://api.map.baidu.com/location/ip?ak=620crf2HSGl1vpSRXFkV66hPodqPszso&coor=bd09ll",
				async:true,
				dataType: 'jsonp',
				success: function(res) {
					console.log(res)
					that.city = res.content.address_detail.city;
				}
			});
		}
	}
</script>

<style scoped="scoped">
	header{
		position: fixed;
		top: 0;
		left: 0;
		z-index: 996;
		width: 100%;
		height: 1.133333rem;
		background: #48382b;
		text-align: center;
		line-height: 1.133333rem;
		font-size: 0.426666rem;
		color: white;
	}
	.city{
		position: absolute;
		left: 0.4rem;
		top: 0;
		display: flex;
		align-items: center;
		transition: 0.5s;
	}
	.city img{
		width: 0.533333rem;
		height: 0.533333rem;
		padding-right: 0.266666rem;
	}
	.weather{
		position: absolute;
		right: 0.4rem;
		top: 0;
	}
	.logo {
		width: 0.8rem;
		padding-top: 0.266666rem;
	}
</style>


