import Vue from 'vue'
import Router from 'vue-router'
//()
import HomePage from '@/components/homePage/HomePage'
import ExplorePage from '@/components/explorePage/ExplorePage'
import MyPage from '@/components/myPage/MyPage'
import DiscoverPage from '@/components/discoverPage/DiscoverPage'
import PointPage from '@/components/pointPage/PointPage'
import Search from '@/components/explorePage/search'
//DiscoverPage
import DiscoverClose from '@/components/discoverPage/DiscoverClose'
//Mypage
import MyCollect from '@/components/myPage/MyCollect'
import MyMessage from '@/components/myPage/MyMessage'
import MyScore from '@/components/myPage/MyScore'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'homePage',
      component: HomePage
    },
    {
    	  path: '/explore',
    	  name: 'explorePage',
    	  component: ExplorePage,
    	  children: [{
    	  	path: '/explore/search',
    	  	name: 'search',
    	  	component: Search
    	  }]
    },
    {
      path: '/discover',
      name: 'discoverPage',
      component: DiscoverPage,
      children:[{ 
      	path: "/close", 
      	component: DiscoverClose
      }] 
    },
    {
      path: '/my',
      name: 'myPage',
      component: MyPage,
      children:[
	       {
	      	path: "/collect", 
	      	component: MyCollect
	      },
	      {
	      	path: "/message", 
	      	component: MyMessage
	      },
	      {
	      	path: "/score", 
	      	component: MyScore
	      }
	      
       ]
    },
    {
      path: '/point',
      name: 'pointPage',
      component: PointPage
    }
  ]
})



