<template>
	<div class="mySwiper">
		<swiper :options="swiperOption">
		    <swiper-slide v-for="(slide,index) in banner" :key="index"><img :src="slide" style="width: 100%;height: 5.333333rem;"/></swiper-slide>
		    <div class="swiper-pagination" slot="pagination"></div>
  		</swiper>
	</div>
</template>

<script>
	import Vue from 'vue'
	import VueAwesomeSwiper from 'vue-awesome-swiper'
	Vue.use(VueAwesomeSwiper)
	import { swiper, swiperSlide } from 'vue-awesome-swiper'
	
	export default{
		//banner 
		props:['banner'],
		 components: {
		    swiper,
		    swiperSlide
		 },
		name: 'carrousel',
	    data() {
	      return {
	        swiperOption: {
	          autoplay: 3500,
	          setWrapperSize :true,
	          pagination : '.swiper-pagination',
	          paginationClickable :true,
	          mousewheelControl : true,
	          observeParents:true,
	          autoplayDisableOnInteraction : false,
	          loop:true
	        }
	      }
  }
}
</script>

<style>
	/*.mySwiper{
		z-index: 700;
	}*/
	/**/
	.swiper-pagination-bullet{
		width: 12px;
		height: 12px;
		opacity: 1;
	    	background: white;
	}
	.swiper-pagination-bullet-active {
		width: 12px;
		height: 12px;
	    opacity: 1;
	    background: orange;
	}
</style>


