<template>
	<div class="collect">
		<app-header :title="title" :who="who"></app-header>
		<div class="not">
			<div class="icon"><img src="../../../static/img/notalk.png"/></div>
			<p></p>
		</div>
	</div>
</template>

<script>
	import AppHeader from "../common/AppHeader2"
	export default{
		components:{
			AppHeader
		},
		data:function(){
			return{
				title:"",
				who:"collect"
			}
		},
		methods:{
			go(){
				this.$router.push({path:'/discover'});
				this.$store.state.collect = false;
			}
		}
	}
</script>

<style scoped="scoped">
	.collect{
		width: 100%;
		min-height: 16.453333rem;
		position: absolute;
	}
	.not{
		position: absolute;
		text-align: center;
		top: 6rem;
		left: 3.7rem;
	}
	.icon{
		width: 1.2rem;
		height: 1.2rem;
	}
	.icon img{
		margin-left: 0.65rem;
		width: 100%;
	}
	p{
		margin-top: 0.4rem;
		margin-left: 0.44rem;
		font-size: 0.4rem;
		color: rgba(190,190,190,0.9);
	}
</style>


