<template>
	<div class="explorePage" :style="{minHeight: minH + 'px'}">
		<header>
			<input class="ipt" type="text" v-model="value" name="" id="" value="" placeholder="" />
			<div class="searchWrap"  @click="search">
				<img class="searchImg" src="../../assets/search.png" />
			</div>
		</header>
		<div class="menu">
			<div class="menu_list" v-for="(item,index) in menu">
				<div :class="{menu_list_on:item.bol}" @click="changeMenu(index)">
					<img :src="item.pic" alt="" />
				</div>
				<p>{{item.name}}</p>
			</div>
		</div>
		<!--<router-link to="/explore/search"></router-link>-->
		<div class="tips" v-if="!searchBol">
			<img src="../../../static/img/cray.png" alt="" />
			<p>~</p>
		</div>
		<mt-button id="btn" v-show="searchBol" type="primary" @click.native="handleClick"></mt-button>
		<router-view v-if="bol"></router-view>
		<div class="foods" v-show="searchBol">
			<div id="top" :style="{height: PhT +'px'}"></div>
			<div class="food" v-for="(item,index) in list" @click="toDetail(index)">
				<div :class="{img:true,move:moveBol}">
					<img :src="item.pic" alt="" />
					<p style="text-align: center; width: 100%;">{{item.name}}</p>
				</div>

			</div>
			<div id="bottom" :style="{height: Ph +'px'}"></div>
		</div>
		<transition name="move">
			<detail id="detail" v-if="judge" :style="{left:ll}" @returnBack="returnBack" :which="which"></detail>
		</transition>
	</div>
</template>

<script>
	import axios from 'axios';
	import { Indicator } from 'mint-ui';
	import Detail from '../discoverPage/DiscoverDetail';
	import iconF1 from '../../assets/iconF1.png';
	import iconF2 from '../../assets/iconF2.png';
	import iconF3 from '../../assets/iconF3.png';
	import iconF4 from '../../assets/iconF4.png';
	import iconF5 from '../../assets/iconF5.png';
	import iconF6 from '../../assets/iconF6.png';
	import iconF7 from '../../assets/iconF7.png';
	import iconF8 from '../../assets/iconF8.png';
	export default {
		components: {
			Detail
		},
		data() {
			return {
				before: 0,
				menu: [{
						pic: iconF1,
						bol: true,
						name: ''
					},
					{
						pic: iconF6,
						bol: false,
						name: ''
					},
					{
						pic: iconF5,
						bol: false,
						name: ''
					},
					{
						pic: iconF2,
						bol: false,
						name: ''
					},
					{
						pic: iconF4,
						bol: false,
						name: ''
					},
					{
						pic: iconF8,
						bol: false,
						name: ''
					},
					{
						pic: iconF7,
						bol: false,
						name: ''
					},
					{
						pic: iconF3,
						bol: false,
						name: ''
					},
				],
				minH: 0,
				list: [],
				bol: false,
				Ph: 0,
				PhT: 0,
				change: [],
				moveBol: false,
				value: null,
				searchBol: true,
				ll: '100%',
				disP: false,
				which: 'explore'
			}
		},
		created() {
			var that = this;
			var winW = document.documentElement.clientWidth || window.innerWidth;
			var winH = document.documentElement.clientHeight || window.innerHeight;
			this.minH = winH - 185;
		},
		methods: {
			returnBack (msg) {
				console.log(msg)
			},
			//
			toDetail(index) {
				var that = this;
				console.log(this.list[index].id);
				axios.get('/api/detail', {
					params: {
						id: this.list[index].shop
					}
				}).then(function(res) {
					that.$store.state.del = res.data.detail;
					that.$store.state.judge = true;
					that.ll = '0%';
				}).catch(function(error) {
					console.log(error)
				});
			},
			//
			search() {
				var that = this;
				console.log(this.value)
				Indicator.open({
					text: '...',
					spinnerType: 'snake'
				});
				axios.get('/api/search', {
					params: {
						begin: 0,
						num: 6,
						search: this.value
					}
				}).then(function(res) {
					setTimeout(() => {
						that.list = res.data.search;
						that.searchBol = true;
						Indicator.close();
						if (that.list.length <= 0) {
							that.searchBol = false;
						}
					}, 1500);
					console.log(res.data);
				}).catch(function(error) {
					setTimeout(() => {
						Indicator.close()
						that.searchBol = false;
					}, 500)
					console.log(error);
				});

				this.value = null;
			},
			//
			changeMenu(index) {
				if(this.moveBol) {
					return;
				}
				if(this.before == index) {
					return;
				}
				//
				if(!this.searchBol) {
					Indicator.open({
						text: '...',
						spinnerType: 'snake'
					});
					setTimeout(() => {
						this.searchBol = true;
						Indicator.close();
					}, 2000)

				}
				var that = this;
				this.menu[this.before].bol = false;
				this.menu[index].bol = true;
				this.before = index;
				this.moveBol = true;
				setTimeout(() => {
					this.moveBol = false;
				}, 2000)
				axios.get('/api/menu', {
					params: {
						begin: 0,
						num: 6,
						listClass: that.menu[index].name
					}
				}).then(function(res) {
					setTimeout(() => {
						that.list = res.data.menu;
					}, 1200)
				}).catch(function(error) {
					console.log(error);
				});
			},
			//
			handleClick() {
				var that = this;
				if(this.changeBol) {
					return
				}
				console.log('c')
				axios.get('/api/change', {
					params: {
						begin: 0,
						num: 6,
						listClass: ''
					}
				}).then(function(res) {
					setTimeout(() => {
						that.list = res.data.change;
					}, 600)
					console.log(res.data.change);
				}).catch(function(error) {
					console.log(error);
				});
				this.changeBol = true;
				this.Ph = 400;
				this.PhT = 450;
				setTimeout(() => {
					this.Ph = 0;
					this.PhT = 0;
					setTimeout(() => {
						this.changeBol = false;
					}, 500)
				}, 1000)
			}
		},
		mounted() {
			var that = this;
			axios.get('/api/Explore', {
				params: {
					begin: 0,
					num: 10
				}
			}).then(function(res) {
				that.list = res.data.explore;
				console.log(res.data.explore);
			}).catch(function(error) {
				console.log(error);
			});
		},
		computed: {
	      judge () {
	        		return this.$store.state.judge
	      }
	  }
	}
</script>

<style scoped="scoped">
	::-webkit-scrollbar {
		width: 0;
	}
	
	.explorePage {
		width: 100%;
		padding-top: 1.133333rem;
		padding-bottom: 1.333333rem;
		overflow: hidden;
		position: relative;
		background: #ebeae4;
	}
	
	header {
		position: fixed;
		top: 0;
		left: 0;
		z-index: 997;
		width: 100%;
		height: 1.133333rem;
		background: #48382b;
		text-align: center;
		line-height: 1.133333rem;
		font-size: 0.426666rem;
		color: white;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	
	header input {
		background: white;
		border-radius: 0.2rem;
		height: 0.533333rem;
	}
	
	input::-webkit-input-placeholder,
	textarea::-webkit-input-placeholder {
		font-size: 0.32rem;
		line-height: 0.64rem;
	}
	
	input:-moz-placeholder,
	textarea:-moz-placeholder {
		font-size: 0.32rem;
		line-height: 0.64rem;
	}
	
	input::-moz-placeholder,
	textarea::-moz-placeholder {
		font-size: 0.32rem;
		line-height: 0.64rem;
	}
	
	input:-ms-input-placeholder,
	textarea:-ms-input-placeholder {
		font-size: 0.32rem;
		line-height: 0.64rem;
	}
	/**/
	
	.tips {
		width: 100%;
		text-align: center;
		background: #ebeae4;
		height: 6.666666rem;
	}
	
	.tips img {
		margin-top: 2rem;
	}
	
	.tips p {
		padding-top: 0.266666rem;
	}
	/**/
	
	.menu {
		display: flex;
		justify-content: space-around;
		align-items: center;
		flex-wrap: wrap;
		border-bottom: 0.133333rem solid lightgray;
		margin-top: 0.133333rem;
	}
	
	.menu_list {
		width: 2.333333rem;
		text-align: center;
		margin: 0.133333rem 0;
	}
	
	.menu_list p {
		padding: 0.066666rem 0;
	}
	
	.menu_list div {
		width: 1.333333rem;
		height: 1.333333rem;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		overflow: hidden;
		margin: 0 auto;
	}
	
	.menu_list_on {
		box-shadow: 0 0 20px 1px red;
	}
	
	.menu_list img {
		width: 1.333333rem;
	}
	/**/
	
	.foods {
		display: flex;
		justify-content: space-around;
		align-items: center;
		flex-wrap: wrap;
		position: relative;
	}
	
	.img {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 4rem;
		height: 3rem;
		background: white;
		margin: 0.2rem 0;
		flex-wrap: wrap;
		padding-top: 0.266666rem;
	}
	
	.food:nth-of-type(2n) {
		margin-left: 0.266666rem;
	}
	
	.food:nth-of-type(2n+1) {
		margin-right: 0.266666rem;
	}
	
	.food img {
		width: 3.333333rem;
	}
	/**/
	
	#btn {
		width: 2rem;
		overflow: hidden;
		margin: 0.133333rem auto;
		display: block;
		font-size: 0.32rem;
	}
	
	#top {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		background: #ebeae4;
		transition: 0.5s ease-out;
	}
	
	#bottom {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		background: #ebeae4;
		transition: 0.5s ease-out;
	}
	
	.move {
		animation: sanke 2s ease-out 0s forwards;
	}
	
	@keyframes sanke {
		0% {
			transform: translateX(5px);
		}
		5% {
			transform: translateX(-5px);
		}
		10% {
			transform: translateX(5px);
		}
		15% {
			transform: translateX(-5px);
		}
		20% {
			transform: translateX(5px);
		}
		25% {
			transform: translateX(-5px);
		}
		30% {
			transform: translateX(5px);
		}
		35% {
			transform: translateX(0px);
			opacity: 1;
		}
		65% {
			transform: translateY(1000px);
			opacity: 0;
		}
		70% {
			transform: translateY(0px);
			opacity: 0;
		}
		100% {
			opacity: 1;
		}
	}
	/**/
	#detail{
		position: absolute;
		top: 0;
		z-index: 998;
		padding-bottom: 1.333333rem;
		background: white;
	}
	.move-enter-active {
		transition: all .5s ease;
	}
	
	.move-leave-active {
		transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
	}
	
	.move-enter,
	.move-leave-to
	{
		transform :translate3d(100%,0,0);
		opacity: 0;
	}
	.searchImg {
		width: 0.853333rem;
	}
	.searchWrap {
		padding-left: 0.266666rem;
		height: 0.853333rem;
	}
	.ipt {
		height: 0.64rem;
	}
</style>


