<template>
	<div>
		<app-header :title="title" :who="who"></app-header>
		<div class="log">
			<div class="head">
				<div class="notlog"><img src="../../../static/img/gentleman.png"/></div>
			</div>
			<div class="import">
				<label class="user common">
					<span>:</span>
					<input type="text" name="user"  placeholder="" v-model="user" v-on:blur="checkuser"/>
					<img :src="checkImg[usercount]" v-show="testuser"/>
				</label>
				<label class="pwd common">
					<span> :</span>
					<input type="password" name="pwd"  placeholder="" class="pwd" v-model="pwd"/>
				</label>
				<label class="repwd common">
					<span>:</span>
					<input type="password" name="repwd"  placeholder="" class="repwd" v-model="repwd" v-on:blur="checkpwd"/>
					<img :src="checkImg[pwdcount]" v-show="testpwd"/>
				</label>
				<!--<label>
					<span>:</span>
					<input type="text" name="code" placeholder="" class="code" v-model="code"/>
					<p></p>
				</label>				-->
				<input type="button"value="" class="sub" v-on:click="reg(user,pwd)"/>
			</div>
		</div>
		  
	</div>
</template>

<script>
	import AppHeader from "../common/AppHeader2"
	import axios from 'axios';
	import { Spinner } from 'mint-ui';
	import { Toast } from 'mint-ui'
	export default{
		components:{
			AppHeader
		},
		data:function(){
			return{
				title:"",
				who:"reg",
				show:false,
				head:"../../../static/img/gentleman.png",
				checkImg:["../../../static/img/checkdown.png","../../../static/img/checkon.png"],
				user:"",
				pwd:"",
				repwd:"",
				testuser:false,
				testpwd:false,
				usercount:0,
				pwdcount:0
			}
		},
		methods:{
			reg:function(user,pwd){
				if(this.usercount==0){
					Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
					return
				}
				if(this.pwdcount==0){
					Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
					return
				}
				var _this = this
				axios.post("/MyPage/res",{user:user,pwd:pwd})
				.then(function(response){
					Toast({
					  message: '',
					  iconClass:"mintui mintui-field-success",
					  duration: 1000,
					  className:"tip"
					});
					_this.$store.state.user = response.data.people;
					setTimeout(function(){
						_this.$store.state.reg = false;
						_this.$store.state.success = true;
						_this.$store.state.myPage_show = true;
					},1000)
				})
				
				
			},
			checkuser:function(){
				var r = /^1[34578]\d{9}$/;
				var bol = r.test(this.user);
				if(bol){
					this.usercount = 1
				}else{
					this.usercount = 0
					Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
				}
				this.testuser = true;
			},
			checkpwd:function(){
				if(!this.pwd){
					this.pwdcount = 0
					this.testpwd = true;
					return
				}
				if(this.pwd===this.repwd){
					this.pwdcount = 1
				}else{
					this.pwdcount = 0
				}
				this.testpwd = true;
			}
			
	},
	
}
</script>

<style scoped="scoped">
	.fl{float: left;}
	.fr{float: right;}
	.clear:after{
		content: "";
		display: block;
		clear: both;
	}
	/**/
	.head{
		width: 100%;
		height: 3.3rem;
		text-align: center;
		position: relative;
	}
	.notlog{
		width: 2.666666rem;
		height: 2.666666rem;
		background: #ebeae4;
		border-radius: 0.5rem;
		position: absolute;
		left: 50%;
		top: 0;
		margin-top: 0.6rem;
		margin-left: -1.333333rem;
	}
	.notlog>img{
		width: 90%;
		height: 90%;
		margin-top: 0.133333rem;
	}
	.log{
		width: 9rem;
		padding: 0.5rem;
		padding-top: 1.64rem;
		height: 14.3rem;
		position: relative;
	}
	/**/
	.import{
		width: 100%;
		margin-top: 0.8rem;
	}
	.common{
		width: 100%;
		display: block;
		line-height: 0.8rem;
		padding-bottom: 0.133333rem;
		/*border-style: none;*/
		border-bottom: 0.013333rem solid rgba(0,0,0,0.5);
		font-size: 0.4rem;
		position: relative;
	}
	.pwd,.repwd{
		margin-top: 0.3rem;
	}
	.common input{
		width:5.6rem;
		margin-left: 0.266666rem;
	}
	.user input{
		margin-left: 0.66rem;
	}
	.pwd input{
		margin-left: 0.96rem;
	}
	.common img{
		position: absolute;
		top: 0.2rem;
		right: 0.133333rem;
		width: 0.4rem;
		height: 0.4rem;
	}
	.pwd img,.repwd img{
		top: 0.36rem;
		right: 0.133333rem;
	}
	
	.sub{
		margin-top: 0.8rem;
		width: 100%;
		background: #48382b;
		color: white;
		line-height: 1rem;
		font-size: 0.4rem;
		border-radius: 0.6rem/0.5rem;
	}
	p{
		width: 100%;
		text-align: center;
		margin-top: 0.6rem;
		font-size: 0.346666rem;
	}
	.other{
		text-align: center;
		margin-top: 0.4rem;
	}
	.other div{
		width: 0.6rem;
		display: inline-block;
		margin-right:0.2rem;
	}
	.other div img{
		width: 100%;
	}
	.base{
		font-size: 0.346666rem;	
		margin-top: 4rem;
		color: rgba(0,0,0,0.6);
	}
</style>


