<template>
	<div>
		<app-header :title="title" :who="who"></app-header>
		<!---->
		<transition @before-enter="begin" @enter="last"mode="out-in">
			<div class="shade" v-show="show"><p></p><mt-spinner type="triple-bounce" :size="60"></mt-spinner></div>
		</transition>
		<div class="log">
			<div class="head">
				<div class="notlog"><img src="../../../static/img/gentleman.png"/></div>
				<transition @before-enter="beforeEnter" @enter="enter"mode="out-in">
					<div class="success" v-show="show"><img :src="head"/></div>
				</transition>
			</div>
			<div class="import">
				<input type="text" name="user"  placeholder="/" class="user" v-model="user"/>
				<input type="password" name="pwd" placeholder="" class="pwd" v-model="pwd"/>
				<input type="button"value="" class="sub" v-on:click="log(user,pwd)"/>
			</div>
			<p>:</p>
			<div class="other">
				<div v-on:click="others"><img src="../../../static/img/qq.png"/></div>
				<div v-on:click="others"><img src="../../../static/img/wechat.png"/></div>
				<div v-on:click="others"><img src="../../../static/img/weibo.png"/></div>
			</div>
			<div class="clear base">
				<div class="fl">?</div>
				<div class="fr" v-on:click="New"></div>
			</div>
		</div>
		<transition name="move">
		 	<register v-show="reg" class="reg"></register>
		 </transition>
	</div>
</template>

<script>
	import AppHeader from "../common/AppHeader2"
	import axios from 'axios';
	import { Spinner } from 'mint-ui';
	import register from "./register"
	import { Toast } from 'mint-ui'
	export default{
		components:{
			AppHeader,
			register
		},
		computed: {
	      	reg () {
	        		return this.$store.state.reg
	       }
	      },
		data:function(){
			return{
				title:"",
				who:"log",
				show:false,
				head:"../../../static/img/gentleman.png",
				user:"",
				pwd:"",
			}
		},
		methods:{
			log:function(user,pwd){
				var _this = this
				axios.post("/MyPage/log",{user:user,pwd:pwd})
				.then(function(response){
					_this.head = response.data.people.head;
					_this.show = true;
					_this.$store.state.user = response.data.people
					setTimeout(function(){
						_this.show = false;
						_this.$store.state.myPage_show = true;
						_this.$store.state.success = true;
					},2000)
				})
				.catch(function(err){
			       Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
			    })
			},
			beforeEnter:function(el){
				$(el).css({
					top:0,
					width: "2.666666rem",
					height:"2.666666rem"
				})
			},
			enter:function(el,done){
				$(el).animate({
					top:"4rem",
					width: "4rem",
					height:"4rem",
					marginLeft:"-1.9rem"
				},2000)
			},
			begin:function(el){
				$(el).css({
					backgroundColor:"white",
					color:"#48382b"
				})
			},
			last:function(el,done){
				$(el).animate({
					backgroundColor:"#948476",
					color:"white"
				},2000)
			},
			// 
			New(){
				this.$store.state.reg = true;
			},
			//
			others(){
				Toast({
					  message: '',
					  iconClass:"mintui mintui-field-warning",
					  duration: 1000,
					  className:"tip"
					});
			}
		}
	}
</script>

<style scoped="scoped">
	.fl{float: left;}
	.fr{float: right;}
	.clear:after{
		content: "";
		display: block;
		clear: both;
	}
	/**/
	.shade{
		width: 100%;
		height: 15.333333rem;
		position: absolute;
		z-index: 100;
		background: white;
		color: #48382b;
		padding-top: 1.64rem;
	}
	.shade p{
		margin-top: 10rem;
		font-size: 0.4rem;
	}
	/**/
	.head{
		width: 100%;
		height: 3.3rem;
		text-align: center;
		position: relative;
	}
	.notlog{
		width: 2.666666rem;
		height: 2.666666rem;
		background: #ebeae4;
		border-radius: 0.5rem;
		position: absolute;
		left: 50%;
		top: 0;
		margin-top: 0.6rem;
		margin-left: -1.333333rem;
	}
	.notlog>img{
		width: 90%;
		height: 90%;
		margin-top: 0.133333rem;
	}
	.success{
		width: 2.666666rem;
		height: 2.666666rem;
		position: absolute;
		left: 50%;
		top: 0;
		margin-top: 0.6rem;
		margin-left: -1.333333rem;
		z-index: 101;
	}
	.success>img{
		width: 100%;
		height: 100%;
		border-radius: 0.5rem;
	}
	.log{
		padding: 0.5rem;
		padding-top: 1.64rem;
		height: 14.3rem;
		position: relative;
	}
	/**/
	.import{
		margin-top: 0.8rem;
	}
	.user,.pwd{
		width: 100%;
		line-height: 0.8rem;
		border-style: none;
		border-bottom: 0.013333rem solid rgba(0,0,0,0.5);
		font-size: 0.4rem
	}
	.pwd{
		margin-top: 0.4rem;
	}
	.sub{
		margin-top: 0.8rem;
		width: 100%;
		background: #48382b;
		color: white;
		line-height: 1rem;
		font-size: 0.4rem;
		border-radius: 0.6rem/0.5rem;
	}
	p{
		width: 100%;
		text-align: center;
		margin-top: 0.6rem;
		font-size: 0.346666rem;
	}
	.other{
		text-align: center;
		margin-top: 0.4rem;
	}
	.other div{
		width: 0.6rem;
		display: inline-block;
		margin-right:0.2rem;
	}
	.other div img{
		width: 100%;
	}
	.base{
		font-size: 0.346666rem;	
		margin-top: 4rem;
		color: rgba(0,0,0,0.6);
	}
	.reg{
		width: 100%;
		height: 16.453333rem;
		background: white;
		position: absolute;
		left: 0;
		top: 0;
	}
	/**/
	.move-enter-active,.move-leave-active{
		transition: all 0.5s ease-out; 
	}
	.move-enter{
		opacity: 0;
	}
	.move-leave-active{
		opacity: 0;
	}
</style>


