<template>
	<div id="more" :style="{width: winW + 'px',height: winH + 'px',left:ll}" @click="dis">
		<div class="wrapRe" v-show="!judgeMore" v-if="wrapBol" v-infinite-scroll="loadMoreRE" infinite-scroll-disabled="loading" infinite-scroll-distance="10" infinite-scroll-immediate-check="false">
			<div class="listRe" v-for="(item,index) in moreReload" @click="RetoDetail(index)">
				<img :src="item.pic" />
				<div class="listRe_value">
					<p style="color: #5b5b5b;">{{item.name}}</p>
					<p style="color: red;">{{item.money}}</p>
					<p style="color: #494949;">{{item.shop}}</p>
				</div>
			</div>
		</div>
		<div class="wrapSc" v-show="!judgeMore" v-if="!wrapBol" v-infinite-scroll="loadMoreSC" infinite-scroll-disabled="loading" infinite-scroll-distance="10" infinite-scroll-immediate-check="false">
			<div class="scale_list" v-for="(item,index) in moreScload" :key="index" @click="SctoDetail(index)">
				<p>{{item.title}}</p>
				<div class="scale_list_time">
					<div>
						<img src="../../assets/timeOut.png" alt="" />:
						<img src="../../assets/timeOut.png" alt="" />:
						<img src="../../assets/timeOut.png" alt="" />
					</div>
					<div id="scale_time">
						<span>0{{item.time.hour}}</span>
						<span>{{item.time.minute>9?'':0}}{{item.time.minute}}</span>
						<span>{{item.time.second>9?'':0}}{{item.time.second}}</span>
					</div>
					<span id="scale_txt">{{item.scale}}</span>
				</div>
				<div class="img">
					<img :src="item.src" alt="" />
				</div>
			</div>
		</div>
		<div class="reload" @click="reloadRe" v-show="!judgeMore" v-if="wrapBol">{{loadTxt}}</div>
		<div class="reload" @click="reloadSc" v-show="!judgeMore" v-if="!wrapBol">{{loadTxt}}</div>
		<transition name="moveDetail">
			<detail id="detail" v-if="judgeMore" :style="{left:morell}" :which="which"></detail>
		</transition>
	</div>
</template>

<script>
	import axios from 'axios';
	import { Indicator } from 'mint-ui';
	import Detail from '../discoverPage/DiscoverDetail';
	export default {
		name: 'more',
		props: ['value'],
		components: {
			Detail
		},
		data() {
			return {
				loadTxt: '',
				winW: 0,
				winH: 0,
				ll: '100%',
				moreRecommend: [],
				Reload: [],
				wrapBol: null,
				scale: [],
				Scload: [],
				timeOut: [],
				scaleIndex: 0,
				recommendIndex: 0,
				loading: false,
				which: 'more',
				morell: '100%'
			}
		},
		methods: {
			//
			RetoDetail(index) {
				console.log(index)
				var that = this;
				axios.get('/api/detail', {
					params: {
						id: this.moreReload[index].shop
					}
				}).then(function(res) {
					that.$store.state.del = res.data.detail;
					that.$store.state.judgeMore = true;
					that.morell = '0%';
				}).catch(function(error) {
					console.log(error)
				});
			},
			//
			SctoDetail(index) {
				console.log(this.moreScload[index].id)
				var that = this;
				axios.get('/api/detail', {
					params: {
						id: this.moreScload[index].title
					}
				}).then(function(res) {
					that.$store.state.del = res.data.detail;
					that.$store.state.judgeMore = true;
					that.morell = '0%';
				}).catch(function(error) {
					console.log(error)
				});
			},
			dis() {
				this.$emit('getBol', {
					ll: '100%'
				})
			},
			reloadRe() {
				var that = this;
				Indicator.open({
					text: '...',
					spinnerType: 'fading-circle'
				});
				axios.get('/api/reload', {
					params: {
						begin: that.recommendIndex,
						num: 10
					}
				}).then(function(res) {			
					console.log(res.data.reload);
					setTimeout(() => {
						that.Reload = res.data.reload;
						that.recommendIndex += 10;
						Indicator.close();
					}, 500)
				}).catch(function(error) {
					console.log(error);
					setTimeout(() => {
						Indicator.close();
						that.loadTxt = '~'
					}, 2000)
				});
			},
			reloadSc() {
				var that = this;
				Indicator.open({
					text: '...',
					spinnerType: 'fading-circle'
				});
				axios.get('/api/scload', {
					params: {
						begin: that.scaleIndex,
						num: 10
					}
				}).then(function(res) {	
					console.log(res.data.scload);
					setTimeout(() => {
						that.Scload = res.data.scload;
						that.scaleIndex += 10;
						Indicator.close();
					}, 500)
				}).catch(function(error) {
					console.log(error);
					setTimeout(() => {
						Indicator.close();
						that.loadTxt = '~'
					}, 2000)
				});
			},
			loadMoreRE() {
				if (this.loadTxt == '~' || this.$store.state.judgeMore == true) {
					return;
				}
				console.log(this.recommendIndex)
				var that = this;
				this.loading = true;
				Indicator.open({
					text: '...',
					spinnerType: 'fading-circle'
				});
				axios.get('/api/reload', {
					params: {
						begin: that.recommendIndex,
						num: 10
					}
				}).then(function(res) {			
					console.log(res.data.reload);
					setTimeout(() => {
						that.Reload = res.data.reload;
						that.recommendIndex += 10;
						Indicator.close();
						that.loading = false;
					}, 1000)
				}).catch(function(error) {
					console.log(error);
					setTimeout(() => {
						Indicator.close();
						that.loadTxt = '~';
						that.loading = false;
					}, 2000)
				});
			},
			loadMoreSC() {
				if (this.loadTxt == '~' || this.$store.state.judgeMore == true) {
					return;
				}
				console.log(this.scaleIndex)
				var that = this;
				this.loading = true;
				Indicator.open({
					text: '...',
					spinnerType: 'fading-circle'
				});
				axios.get('/api/scload', {
					params: {
						begin: that.scaleIndex,
						num: 10
					}
				}).then(function(res) {			
					console.log(res.data.scload);
					setTimeout(() => {
						that.Scload = res.data.scload;
						that.scaleIndex += 10;
						Indicator.close();
						that.loading = false;
					}, 1000)
				}).catch(function(error) {
					console.log(error);
					setTimeout(() => {
						Indicator.close();
						that.loadTxt = '~';
						that.loading = false;
					}, 2000)
				});
			}
		},
		computed: {
			moreReload() {
				this.moreRecommend = this.moreRecommend.concat(this.Reload)
				return this.moreRecommend
			},
			moreScload() {
				this.scale = this.scale.concat(this.Scload)
				return this.scale
			},
			judgeMore () {
	        		return this.$store.state.judgeMore
	      }
		},
		created() {
			console.log(this.scaleIndex)
			var that = this;
			var winW = document.documentElement.clientWidth || window.innerWidth;
			var winH = document.documentElement.clientHeight || window.innerHeight;
			this.winW = winW;
			this.winH = winH;
			this.ll = '100%';
			var time = new Date();
			this.timeOut.time = time.getTime();
			if(!localStorage.time) {
				localStorage.time = this.timeOut.time;
			}
			var second = parseInt((this.timeOut.time - localStorage.time) / 1000 % 60)
			var minute = parseInt((this.timeOut.time - localStorage.time) / 1000 / 60 % 60)
			if(this.value.name == 'moreScale') {
				that.wrapBol = false;
				axios.get('/api/scload', {
					params: {
						begin: that.scaleIndex,
						num: 10
					}
				}).then(function(res) {
					that.scale = res.data.moreScale;
					for(let i = 0; i < that.scale.length; i++) {
						if(that.scale[i].time.minute - minute >= 0) {
							that.scale[i].time.minute = that.scale[i].time.minute - minute;
						} else {
							that.scale[i].time.minute = 60 + that.scale[i].time.minute - minute;
						}
						if(that.scale[i].time.second - second >= 0) {
							that.scale[i].time.second = that.scale[i].time.second - second;
						} else {
							that.scale[i].time.second = 60 + that.scale[i].time.second - second;
							that.scale[i].time.minute--;
						}

					}
					that.scaleIndex += 10;
				}).catch(function(error) {
					console.log(error);
				});
				//				
				setInterval(function() {
					for(let i = 0; i < that.scale.length; i++) {
						that.scale[i].time.second--;
						if(that.scale[i].time.second < 0) {
							that.scale[i].time.second = 59;
							that.scale[i].time.minute--;
							if(that.scale[i].time.minute < 0) {
								that.scale[i].time.minute = 59;
								that.scale[i].time.hour--;
								if(that.scale[i].time.hour < 0) {
									that.scale[i].time.hour = 1;
								}
							}
						}
					}
				}, 1000)
			}

			if(this.value.name == 'moreRecommend') {
				that.wrapBol = true;
				axios.get('/api/reload', {
					params: {
						begin: that.recommendIndex,
						num: 10
					}
				}).then(function(res) {
					that.moreRecommend = res.data.moreRecommend;
					that.recommendIndex += 10;
				}).catch(function(error) {
					console.log(error);
				});
			}
		},
		updated() {}
	}
</script>

<style scoped="scoped">
	#more {
		/*position: absolute;
		top: 0;*/
		padding-top: 0.266666rem;
		background: white;
		z-index: 990;
		overflow: scroll;
	}
	/**/
	
	.listRe {
		border-top: 0.133333rem solid lightgray;
	}
	
	.listRe_value {
		width: 9.333333rem;
		margin: 0 auto;
		padding: 0.133333rem 0 0.266666rem 0;
	}
	
	.listRe_value>p {
		padding: 0.066666rem 0;
	}
	
	.listRe img {
		width: 100%;
	}
	/**/
	
	.wrapSc {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		width: 9.333333rem;
		margin: 0 auto;
	}
	
	.scale_list {
		/*display: flex;*/
		width: 4.4rem;
		margin: 0.266666rem 0;
		padding: 0 0.066666rem;
		/*box-shadow: 0px 0px 0.013333rem 0.013333rem gold;*/
	}
	
	.scale_list>p:nth-of-type(1) {
		text-align: center;
		color: #5b5b5b;
		font-size: 0.32rem;
		padding-bottom: 0.266666rem;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	
	#scale_txt {
		color: red;
		font-size: 0.4rem;
	}
	
	.scale_list .scale_list_time {
		height: 0.6rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
		position: relative;
	}
	
	.scale_list_time img {
		width: 0.533333rem;
	}
	
	.scale_list_time div {
		width: 2rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	
	.scale_list_time #scale_time {
		color: white;
		position: absolute;
		left: 0;
		top: 0.08rem;
	}
	
	#scale_time span {
		text-align: center;
		width: 0.533333rem;
	}
	
	.scale_list .img {
		width: 4.4rem;
		height: 2.666666rem;
		overflow: hidden;
	}
	
	.img {
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0.133333rem 0;
	}
	.img img{
		width: 100%;
	}
	/**/
	
	.reload {
		text-align: center;
		background: orange;
		line-height: 0.853333rem;
	}
	/**/
	#detail{
		position: absolute;
		top: 0;
		z-index: 998;
		padding-bottom: 1.333333rem;
		background: white;
	}
	.moveDetail-enter-active {
		transition: all .5s ease;
	}
	
	.moveDetail-leave-active {
		transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
	}
	
	.moveDetail-enter,
	.moveDetail-leave-to
	{
		transform :translate3d(100%,0,0);
		opacity: 0;
	}
</style>


